// Special styling for alumni portraits
.portrait[data-style="small"] {
  // Make alumni portraits wider to accommodate names
  width: 150px;

  .portrait-name {
    // Prevent name breaking into multiple lines
    white-space: nowrap;
  }

  .portrait-period {
    display: block;
    font-size: 0.9em;
    color: var(--text);
    opacity: 0.8;
    margin-top: 5px;
  }
}